import React from 'react';
import './SomosRefi.scss';
import somosRefiImg from '../assets/somos_refi_foto.jfif'
import refiLogoImg from '../assets/refi blanco-01.png'

const SomosRefi = () => {

  return (
    <section id="somos-refi" className="somos-refi-section">
      <div className="somos-refi-content">
        <div className="image">
          <img src={somosRefiImg} alt="Imagen Somos Refi" />
        </div>
        <div className="text-container">
          <div className="title-container">
            <h2>SOMOS</h2>
            <img src={refiLogoImg} />
          </div>
          <div className="text">
            <p>
              La escuela, la tcnica 10, la de calle Velez Sarfield 641, es hoy una refinera de
              contenidos, para el desarrollo y la innovacin cultural.
            </p>
            <p>
              Refuncionalizando y poniendo en valor lo que supo ser, en 1936, la primera
              escuela tcnica de la ciudad, una experiencia de taller-escuela orientada a los
              oficios electromecnicos que brind educacin pblica y gratuita a los hijos
              e hijas de una gran cantidad de inmigrantes que llegaban a borbotones del
              viejo continente.
            </p>
            <p>
              Refi es completamente amplia, tiene espacio para todos y todas, de puertas abiertas para pasar el da, trabajar o venir a disfrutar de las diferentes movidas. Son aproximadamente 1600 mts2 de infraestructura y 100 mts de largo.
              Desde el inicio, en la comunidad funciona Refi Caf, un espacio gastronmico
              que abre de martes a domingo de 9 a 13hs y de 16.30 a 20.30hs ofreciendo
              cafetera de especialidad y una carta de comida honesta para disfrutar los
              mejores desayunos y meriendas, siempre con la posibilidad de degustar los
              mejores tragos.
            </p>
            <p>
              La amplia capacidad de espacio dispone de diferentes zonas, un aulario
              restaurado donde se dictan cursos y seminarios. Tambin existe un estudio de
              grabacin y un nuevo espacio para el desarrollo y ejecucin de espectculos
              literarios, artsticos, musicales y teatrales con capacidad de hasta seiscientas
              personas, sonido, escenario y plantas de luces propia.
            </p>
            <p>
              Refi, adems, cuenta con personal profesional en cada una de las reas
              que abarca: gastronoma, docencia, comunicacin, espectculos, sonido, iluminacin, escenario, construccin y software.
            </p>
            <span>
              Refi es cultura
            </span>
            <br />
            <span>
              Refi es encuentro
            </span>
            <br />
            <span>
              Refi es diversin
            </span>
            <br />
            <span>
              Refi es comunidad
            </span>
            <br />
            <br />
            <span>
              Refi es porvenir, y lo que se viene es para compartirlo con vos.
            </span>
          </div>
        </div>
      </div>
    </section>
  );
};

export default SomosRefi;
